.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
}

.home-content {
  text-align: center;
  max-width: 600px;
  padding: 20px;
}

h1 {
  font-size: 3rem;
  color: #333;
}

p {
  font-size: 1.2rem;
  margin: 20px 0;
  color: #666;
}

.home-button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.home-button:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }

  p {
    font-size: 1rem;
  }

  .home-button {
    font-size: 0.9rem;
  }
}
