.about-container {
  padding: 50px;
  max-width: 900px;
  margin: 0 auto;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

h1, h2 {
  color: #333;
  margin-bottom: 20px;
}

h1 {
  font-size: 2.5rem;
  text-align: center;
  color: #007bff;
  margin-bottom: 40px;
}

h2 {
  font-size: 1.8rem;
  margin-top: 40px;
  border-left: 4px solid #007bff;
  padding-left: 10px;
}

p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #555;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding-left: 0;
}

ul li {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 10px;
  padding-left: 25px;
  position: relative;
}

ul li:before {
  content: "•";
  color: #007bff;
  position: absolute;
  left: 0;
  font-size: 1.5rem;
  line-height: 0;
}

.about-container a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.about-container a:hover {
  color: #0056b3;
}

@media (max-width: 768px) {
  .about-container {
    padding: 30px;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p, ul li {
    font-size: 1rem;
  }
}
